'use strict';


const
    SHAPE_CIRCLE = 0,
    SHAPE_SQUARE = 1;

class IconBase {

    static ID() {
        return 'base 1';
    }

    /**
     * @param center - of the icon base relative to the underlying canvas
     * @param radius - of the icon base
     */
    constructor(center, radius) {
        this.circleBasePath = new paper.Path.Circle({
            center: center,
            radius: radius,
            shadowColor: new paper.Color(0, 0, 0, 0.4),
            shadowBlur: 2,
            shadowOffset: new paper.Point(0, 1),
            name: IconBase.ID()
        });
        this.circleBasePath.sendToBack();
        this.shape = SHAPE_CIRCLE;
        let scoreStencil = new paper.Shape.Rectangle(
            new paper.Point(center.x - radius, center.y - radius),
            new paper.Point(center.x + radius, center.y)
        );
        this.circleBaseScorePath = this.circleBasePath.toPath().intersect(scoreStencil.toPath());
        this.circleBaseScorePath.fillColor = 'rgba(0, 0, 0, 0.1)';
        this.circleBaseScorePath.visible = false;

        this.squareBasePath = new paper.Path.Rectangle({
            point: new paper.Point(center.x - radius, center.y - radius),
            size: new paper.Size(radius * 2, radius * 2),
            radius: 2,
            shadowColor: this.circleBasePath.shadowColor,
            shadowBlur: this.circleBasePath.shadowBlur,
            shadowOffset: this.circleBasePath.shadowOffset,
            name: IconBase.ID()
        });
        this.squareBasePath.sendToBack();
        this.squareBasePath.visible = false;

        this.squareBaseScorePath = this.squareBasePath.toPath().intersect(scoreStencil.toPath());
        this.squareBaseScorePath.fillColor = 'rgba(0, 0, 0, 0.1)';
        this.squareBaseScorePath.visible = false;

        this.scoreEffectEnabled = false;
    }

    setSquareShape() {
        this.circleBasePath.visible = false;
        this.squareBasePath.visible = true;
        this.shape = SHAPE_SQUARE;
        this.setScoreEffect(this.scoreEffectEnabled);
    }

    setCircularShape() {
        this.circleBasePath.visible = true;
        this.squareBasePath.visible = false;
        this.shape = SHAPE_CIRCLE;
        this.setScoreEffect(this.scoreEffectEnabled);
    }

    setColor(color) {
        this.circleBasePath.fillColor = color;
        this.squareBasePath.fillColor = color;
    }

    setCenter(center) {
        this.circleBasePath.center = center;
        this.squareBasePath.center = center;
    }

    setScoreEffect(enableScore) {
        this.scoreEffectEnabled = enableScore;
        this.circleBaseScorePath.visible = false;
        this.squareBaseScorePath.visible = false;
        if (enableScore) {
            if (this.shape === SHAPE_CIRCLE) this.circleBaseScorePath.visible = true;
            else if (this.shape === SHAPE_SQUARE) this.squareBaseScorePath.visible = true;
        }
    }

    getPathWithoutShadows() {
        if (this.shape === SHAPE_CIRCLE) return this.circleBasePath;
        else if (this.shape === SHAPE_SQUARE) return this.squareBasePath;
        console.warn('unknown base shape type ' + this.shape);
    }

    /**
     * Remove this base + shadow from canvas.
     */
    remove() {
        this.circleBasePath.remove();
        this.circleBaseScorePath.remove();
        this.squareBasePath.remove();
        this.squareBaseScorePath.remove();
    }

}

module.exports = IconBase;
